'use strict';
var multiItemSlider = (function () {
  return function (selector, config) {
    var _mainElement = document.querySelector(selector), // Ð¾ÑÐ½Ð¾Ð²Ð½Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð±Ð»Ð¾ÐºÐ°
      _sliderWrapper = _mainElement.querySelector('.slider__wrapper'), // Ð¾Ð±ÐµÑ€Ñ‚ÐºÐ° Ð´Ð»Ñ .slider-item
      _sliderItems = _mainElement.querySelectorAll('.slider__item'), // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ (.slider-item)
      _sliderControls = _mainElement.querySelectorAll('.slider__control'), // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ
      _sliderControlLeft = _mainElement.querySelector('.slider__control_left'), // ÐºÐ½Ð¾Ð¿ÐºÐ° "LEFT"
      _sliderControlRight = _mainElement.querySelector(
        '.slider__control_right',
      ), // ÐºÐ½Ð¾Ð¿ÐºÐ° "RIGHT"
      _wrapperWidth = parseFloat(getComputedStyle(_sliderWrapper).width), // ÑˆÐ¸Ñ€Ð¸Ð½Ð° Ð¾Ð±Ñ‘Ñ€Ñ‚ÐºÐ¸
      _itemWidth = parseFloat(getComputedStyle(_sliderItems[0]).width), // ÑˆÐ¸Ñ€Ð¸Ð½Ð° Ð¾Ð´Ð½Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
      _positionLeftItem = 0, // Ð¿Ð¾Ð·Ð¸Ñ†Ð¸Ñ Ð»ÐµÐ²Ð¾Ð³Ð¾ Ð°ÐºÑ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
      _transform = 0, // Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ñ‚Ñ€Ð°Ð½Ñ„ÑÐ¾Ñ„Ñ€Ð¼Ð°Ñ†Ð¸Ð¸ .slider_wrapper
      _step = (_itemWidth / _wrapperWidth) * (100 % -120), // Ð²ÐµÐ»Ð¸Ñ‡Ð¸Ð½Ð° ÑˆÐ°Ð³Ð° (Ð´Ð»Ñ Ñ‚Ñ€Ð°Ð½ÑÑ„Ð¾Ñ€Ð¼Ð°Ñ†Ð¸Ð¸)
      _items = []; // Ð¼Ð°ÑÑÐ¸Ð² ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð²
    // Ð½Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ Ð¼Ð°ÑÑÐ¸Ð²Ð° _items
    _sliderItems.forEach(function (item, index) {
      _items.push({ item: item, position: index, transform: 0 });
    });

    var position = {
      getMin: 0,
      getMax: _items.length - 1,
    };

    var _transformItem = function (direction) {
      if (direction === 'right') {
        if (
          _positionLeftItem + _wrapperWidth / _itemWidth - 1 >=
          position.getMax
        ) {
          return;
        }
        if (!_sliderControlLeft.classList.contains('slider__control_show')) {
          _sliderControlLeft.classList.add('slider__control_show');
        }
        if (
          _sliderControlRight.classList.contains('slider__control_show') &&
          _positionLeftItem + _wrapperWidth / _itemWidth >= position.getMax
        ) {
          _sliderControlRight.classList.remove('slider__control_show');
        }
        _positionLeftItem++;
        _transform -= _step;
      }
      if (direction === 'left') {
        if (_positionLeftItem <= position.getMin) {
          return;
        }
        if (!_sliderControlRight.classList.contains('slider__control_show')) {
          _sliderControlRight.classList.add('slider__control_show');
        }
        if (
          _sliderControlLeft.classList.contains('slider__control_show') &&
          _positionLeftItem - 1 <= position.getMin
        ) {
          _sliderControlLeft.classList.remove('slider__control_show');
        }
        _positionLeftItem--;
        _transform += _step;
      }
      _sliderWrapper.style.transform = 'translateX(' + _transform + '%)';
    };

    const list = document.querySelector('.testimonials-thumb-list');
    console.log(list);
    _sliderControlRight.addEventListener('click', handler);
    _sliderControlLeft.addEventListener('click', onHandler);

    function handler() {
      list.style.transform = 'translateX(' + (_transform - 100) + 'px)';
    }

    function onHandler() {
      list.style.transform = 'translateX(' + (_transform + 100) + 'px)';
    }

    // Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ click Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿Ð¾Ðº "Ð½Ð°Ð·Ð°Ð´" Ð¸ "Ð²Ð¿ÐµÑ€ÐµÐ´"
    var _controlClick = function (e) {
      if (e.target.classList.contains('slider__control')) {
        e.preventDefault();
        var direction = e.target.classList.contains('slider__control_right')
          ? 'right'
          : 'left';
        _transformItem(direction);
      }
    };

    var _setUpListeners = function () {
      // Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ðº ÐºÐ½Ð¾Ð¿ÐºÐ°Ð¼ "Ð½Ð°Ð·Ð°Ð´" Ð¸ "Ð²Ð¿ÐµÑ€ÐµÐ´" Ð¾Ð±Ñ€Ð±Ð¾Ñ‚Ñ‡Ð¸ÐºÐ° _controlClick Ð´Ð»Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ñ click
      _sliderControls.forEach(function (item) {
        item.addEventListener('click', _controlClick);
      });
    };

    // Ð¸Ð½Ð¸Ñ†Ð¸Ð°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ
    _setUpListeners();

    return {
      right: function () {
        // Ð¼ÐµÑ‚Ð¾Ð´ right
        _transformItem('right');
      },
      left: function () {
        // Ð¼ÐµÑ‚Ð¾Ð´ left
        _transformItem('left');
      },
    };
  };
})();

var slider = multiItemSlider('.slider');
